                background: #ed6a5e;
            }

            .minus {
                background: #f5bf4f;
            }

            .zoom {
                background: #61c554;
            }
        }

        max-width: 80%;
        margin: 5em auto;
        background: $normal;

        .content {
            display: flex;
            background: darken($normal, 3);
            height: 80vh;

            .left {
                overflow: auto;
                max-width: 220px;

                .search {
                    input {
                        margin-top: 1em;
                        height: 20px;
                    }
                }

                .divider{
                    padding: 12.3px;
                    border-bottom: 2px solid lighten($normal, 3);
                    color: rgb(140, 140, 140);
                    font-size: 15px;    
                }

                ul {
                    padding: 0px;
                    margin: 0px;
                    min-width: 200px;

                    li {
                        position: relative;
