                        padding: 15px;
                        color: rgb(140, 140, 140);

                        &.active {
                            background: $normal;
                            color: #fff;
                        }

                        &.notified {
                            color: #fff;
                        }

                        .notifications{
                            position: absolute;       
                            top: 50%;
