                overflow: auto;

                .title {
                    display: flex;
                    align-items: center;
                    padding: 15px;
                    border-bottom: 2px solid darken($normal, 3);
                    overflow: auto;

                    img{
                        margin-right: 10px;
                    }
                }

                .messages {
                    flex: 1;
